<template>
    <div>
        <cube-button @click="showToastTxtOnly">Toast - txt only</cube-button>
    </div>
</template>
<script>
    export default {
        methods: {
            showToastTxtOnly() {
                localStorage.setItem('refresh', 2);
                this.toast = this.$createToast({
                    txt: 'Plain txt',
                    type: 'txt'
                })
                this.toast.show()
            }
        }
    }
</script>